//================================|
//  Font -----------------------|
//================================|
$font: 'Lato', sans-serif;

$text-deco: none;

$ln-height: 1.45em;

//================================|
//  Size -------------------------|
//================================|
$font-size: 1.5em; //blockquote
$width: 100%; //nav link, btn

//================================|
//  Weight -----------------------|
//================================|
$font-weight: 300; //headline, blockquote

//================================|
// $colors -----------------------|
//================================|
$btn-colors:(
    'default': #51ddfc,
    'success': #63cc82,
    'error': #e4757a,
    'warning': #fd7856,
    'info': #927bc1
);

$colors: (
    'nav': #777,
    'primary': #58e1c1,
    'abtn':#51ddfc,
    'pink':#deb2ee,
    'lightblue':#7ee6fd,
    'white': #fff,
    'error':#e4757a,
    'info':#927bc1,
    'bg':#63cc82,
    'warn':#fd7856
);

$col-colors: (
    '1':#927bc1,
    '2':#fd7856,
    '3':#deb2ee,
    '4':#7ee6fd,
    '5':#88d9a0,
    '6':#4da2bb,
    '7':#ec9fa3,
    '8':#fd9f85,
    '9':#e4757a,
    '10':#63cc82,
    '11':#51ddfc,
    '12':#80e8d1
);

//================================|
// $breakpoints ------------------|
//================================|
$breakpoints: (
 'med': 768px,
);

//================================|
// Column flexing ----------------|
//================================|
$col-flex: (
    '1':8.33%,
    '2':16.67%,
    '3':25.00%,
    '4':33.33%,
    '5':41.67%,
    '6':50.00%,
    '7':58.33%,
    '8':66.67%,
    '9':75.00%,
    '10':83.33%,
    '11':91.67%,
    '12':100%
);


