/* ============================================= */
/*              Base styles                      */
/* ============================================= */

* {
    font-family: $font;
    color: map-get($colors, 'nav');
    box-sizing: border-box;
  }
  
  p {
    line-height: $ln-height;
  }
  
  a {
    text-decoration: $text-deco;
  }
  
  
  /* ============================================= */
  /*              Typography                       */
  /* ============================================= */
  
  .headline {
    &-primary {
      color: map-get($colors, 'primary');
      margin-left: 10px;
    }
    &-secondary {
      font-weight: $font-weight;
      color: map-get($colors, 'nav');  
    }
  }

  .link {
    color: map-get($colors, 'abtn');
    font-weight: 700;
  }
  
  .blockquote {
    font-size: 1.5em;
    font-weight: 300;
    font-style: italic;
    margin-left: 20px;
    padding-left: 20px;
    border-left: 8px solid map-get($colors, 'primary');
    border-radius: 5px;
  }