/* ============================================= */
/*              Grid System                      */
/* ============================================= */

.container {
    padding: 0 10px;
      .row {
        display: flex;
        flex-direction: column;
        padding: 1em 10px;
      }
      .card {
        flex-basis: 100%;
      }
      .theme.col{
        @include theme-colors($col-colors);
      }
  }
  
  [class^=col] {
    margin-top: 10px;
    flex-basis: 100%;
    min-height: 25px;
    padding: 10px;
    border-radius: 5px;
  }


